/***/ (function(module, exports) {

	module.exports = {"data":{"wordpressPost":{"title":"Former Friends Star Courteney Cox Talks Cosmetic Surgery Regrets","content":"<p>She&#8217;s best known for her role as 20-something Monica in the hit TV series Friends, but Courteney Cox is actually a grown-up 53. She looks tremendously youthful, unbelievably so and it turns out there&#8217;s a reason for that.</p>\n<p>The actress is blessed with good genes and she has taken incredibly good care of herself too, but she&#8217;s also dabbled in injectables (both <a href=\"http://the-beautyinsider.com/quick-factual-guide-injectables-facts-no-myths/\">fillers </a>and <a href=\"http://the-beautyinsider.com/quick-factual-guide-injectables-facts-no-myths/\">muscle freezers</a>), which has clearly made a massive difference to her appearance. We&#8217;re all good with that here at <strong>The Beauty Insider</strong>. Our feeling is that women (and men) should love what they see in the mirror &#8211; and if that means a tweak or two then we reckon you should go for it.</p>\n<p>But, and this is a big but, you need to be very aware of two things before you do. Firstly, no amount of injectables can change the way you feel about yourself on the inside. That can only come from a hearty dose of self love and appreciation. Secondly, you will only make you look better if the &#8216;work&#8217; is very carefully done and by a doctor, or even surgeon, who leans towards a distinctly natural look.</p>\n<p>This is something that Courteney knows and from experience.</p>\n<figure id=\"attachment_2401\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2401\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/24203310/inblack-JPG-300x295.jpg\" alt=\"\" width=\"300\" height=\"295\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/24203310/inblack-JPG-300x295.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/24203310/inblack-JPG.jpg 599w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@courteneycoxfanpage</figcaption></figure>\n<p>&nbsp;</p>\n<p>In an interview with <a href=\"https://www.newbeauty.com/blog/dailybeauty/11013-courteney-cox-beauty/\">New Beauty</a> the TV star has revealed that she regrets the procedures she has had done and does not plan to have any more. She also reveals she felt under pressure to maintain her youthful looks &#8211; particularly given her work in Hollywood, a notoriously tough place for women over the age of 18.</p>\n<p>&#8220;I grew up thinking that appearance was the most important thing. That&#8217;s kind of sad because it got me in trouble. I was trying so hard to keep up, and I actually made things worse,&#8221; she was quoted as saying in the interview.</p>\n<p>She also revealed that every time she underwent one procedure, the doctors she saw would encourage her to have another.</p>\n<p>&#8220;[W]hat would end up happening is that you go to a doctor who would say, &#8216;You look great, but what would help is a little injection here or filler there.&#8217;</p>\n<p>&#8220;So you walk out and you don&#8217;t look so bad and you think, no one noticed—it&#8217;s good. Then somebody tells you about another doctor: &#8216;This person&#8217;s amazing. They do this person who looks so natural.&#8217; You meet them and they say, &#8216;You should just do this.&#8217; The next thing you know, you&#8217;re layered and layered and layered. You have no idea because it&#8217;s gradual until you go, &#8216;Oh shit, this doesn&#8217;t look right.&#8217; And it&#8217;s worse in pictures than in real life,&#8221; she said.</p>\n<figure id=\"attachment_2402\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2402\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/24203629/Friends-JPG-300x166.jpg\" alt=\"\" width=\"300\" height=\"166\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/24203629/Friends-JPG-300x166.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/24203629/Friends-JPG.jpg 597w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Friends &#8211; Monica, AKA Courteney, third from left.</figcaption></figure>\n<p>&nbsp;</p>\n<p>It took a trusted friend to tell Courteney that she was looking &#8216;done&#8217; and that it wasn&#8217;t good. And thankfully, Courteney listened and listened hard. And she decided to give up on injectables altogether, she said. No more filler, no more muscle freezers.</p>\n<p>&#8220;Everything&#8217;s going to drop,&#8221; she said.</p>\n<p>&#8220;I was trying to make it not drop, but that made me look fake. You need movement in your face, especially if you have thin skin like I do. Those aren&#8217;t wrinkles — they&#8217;re smile lines. I&#8217;ve had to learn to embrace movement and realise that fillers are not my friend,&#8221; she said.</p>\n<p>Courteney said her face is now entirely natural. And heck, if that&#8217;s true then more power to her because she looks incredible. And if it isn&#8217;t, that&#8217;s fine too. Because she looks beautiful &#8211; naturally so &#8211; and that&#8217;s what counts. She&#8217;s now engaged to Snow Patrol musician Johnny McDaid, 40, too so she&#8217;s no doubt happy as a clam. And if anything is going to give you that special glow it&#8217;s that!</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/24200733/%40courteneycoxfanpage-JPG.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"34672d91-eaa4-58b1-8ff0-d4bbf4fbce7c"}}

/***/ })

